<template>
  <div class="container_mouse">
    <div class="mouse_icon"></div>
  </div>
</template>

<script>
export default {
  name: "mouse-scroll",
};
</script>

<style scoped>
.container_mouse {
  position: absolute;
  bottom: 30px;
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 60px 0px 0px 0px;
}
.mouse_icon {
  position: relative;
  width: 20px;
  height: 40px;
  border: 2px solid #0071da;
  z-index: 999;
  border-radius: 20px;
  cursor: pointer;
}
.mouse_icon:after {
  position: absolute;
  top: 4px;
  width: 4px;
  height: 8px;
  content: "";
  background-color: #0071da;
  transform: translateX(-50%);
  left: calc(50% - 0px);
  border-radius: 2px;
  animation: mouse 2s infinite;
}
@media (max-height: 540px) {
  .container_mouse {
    display: none;
  }
}

@keyframes mouse {
  0% {
    opacity: 1;
    top: 4px;
  }
  100% {
    opacity: 0;
    top: 30px;
  }
}
</style>
