export const namespaced = true

export const state = {
    userMenuActive: true,
    dark_mode: false,
    navbar_positions: ["nav_blog", "nav_digests", "nav_services"],
    navbar_dir: 'nav_blog',
    // Panel navbar data
    isOnPanel: false,

}

export const mutations = {
    setNavbarPos(state, direction) {
        if (state.navbar_dir.includes(direction)) {
            state.navbar_dir = direction
        } else {
            state.navbar_dir = state.navbar_positions[0]
        }
    },
}

export const actions = {
    setUserMenuValue({ state }, newValue) {
        state.userMenuActive = newValue
    },
    setOnPanelMenu({ state }, newValue) {
        state.isOnPanel = newValue
    }

}

export const modules = {}