<template>
  <div class="update_container" @click.self="closeTab()">
    <div class="update_window">
      <div class="exit_button">
        <div class="exit_button_img" @click="closeTab()">
          <img src="@/assets/icons/helpers/error.svg" alt="" />
        </div>
      </div>
      <div class="scrollable_container">
        <div class="title__text">Editing Simple Collection NFT</div>
        <div class="generic__text required">
          <h4 class="star">*</h4>
          Required fields
        </div>
        <div class="generic_label">
          Item image
          <h4 class="star">*</h4>
        </div>
        <gf-image-drop-file
          class="img_drop_1"
          v-model="nft_image_updated"
          :defaultURL="nft_to_update.image"
        ></gf-image-drop-file>
        <gf-input-field
          v-model="nft_title_updated"
          :showError="title_error"
          :showRequired="true"
        >
          <template v-slot:title>Title</template>
          <template v-slot:helper>e.g. "My beautiful NFT"</template>
        </gf-input-field>
        <gf-textarea
          v-model="nft_description_updated"
          :showError="description_error"
          :showRequired="true"
        >
          <template v-slot:title>Description</template>
          <template v-slot:helper
            >e.g. "After purchasing you'll be able to enter the VIP area and get
            a T-Shirt"</template
          >
        </gf-textarea>
        <gf-input-field
          v-model="nft_link_updated"
          :showError="external_link_error"
          :showRequired="false"
        >
          <template v-slot:title>External link</template>
          <template v-slot:helper
            >e.g. "mySite.com/NFT" Provide the owner some extra info about your
            NFT</template
          >
        </gf-input-field>
        <div class="properties">
          <div class="subtitle__text">
            Advanced properties
            <img
              src="@/assets/icons/question.svg"
              v-if="!properties_help"
              @click="togglePropsHelp"
            />
            <img
              src="@/assets/icons/question_cancel.svg"
              v-if="properties_help"
              @click="togglePropsHelp"
            />
          </div>
          <div class="generic__text help_props" v-if="properties_help">
            Create your own properties for your NFT which can be used in many
            ways to add more functionalities.
          </div>
          <div class="properties_container" ref="property_container">
            <gf-boxed-input-field
              v-model="nft_props_updated"
              :initValue="nft_to_update.properties"
            >
              <template v-slot:helper_name>e.g. Reserved seat</template>
              <template v-slot:helper_value>e.g. Row B, seat 3</template>
            </gf-boxed-input-field>
          </div>
        </div>
        <div class="space_top">
          <gf-cta-button
            :label="'Update NFT'"
            @click="updateNFT()"
          ></gf-cta-button>
          <gf-delete-button
            :label="'Delete item'"
            @click="deleteNFTItem()"
          ></gf-delete-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "gf-edit-simple-collection-nft",
  props: {
    show: Boolean,
    existing_nft: Object,
  },
  data() {
    return {
      // Vars
      properties_help: false,
      show_window: false,
      nft_props_updated: null,
      nft_image_updated: null,
      nft_title_updated: "",
      nft_description_updated: "",
      nft_link_updated: "",
      // Errors
      errors: {
        username__error: false,
        image_error: false,
        title_error: false,
        description_error: false,
        properties_error: false,
        extra_fields_error: false,
      },
      // Data
      digest_selector: "Select the digest group",
      nft_to_update: {
        id: null,
        username: "",
        image: "",
        title: "",
        description: "",
        link: "",
        properties: [{ name: "", value: "", type: "" }],
        extra_fields: "",
        collection: null,
      },
      init_properties: [],
    };
  },
  methods: {
    togglePropsHelp() {
      this.properties_help = !this.properties_help;
    },
    closeTab() {
      this.$emit("closeTab", false);
    },
    // copyInfo() {
    //   this.nft_to_update = this.existing_nft;
    //   this.init_properties = this.nft_to_update.properties;
    // },
    async deleteNFTItem() {
      let url = this.$store.state.gf_contracts.backend;
      const token = this.$store.state.user_auth_w2.token;
      axios.defaults.headers.common["Authorization"] = "Token " + token;
      //   try {
      await axios
        .delete(
          url + "/nft_simple_collection/" + this.nft_to_update.id + "/delete"
        )
        .then((res) => {
          console.log("RES:", res);
        })
        .catch((e) => {
          console.log(e);
        });
      //   } catch (e) {
      //     console.log("F");
      //   }
    },
    async updateNFT() {
      const fd = new FormData();
      // CHECK UPDATE NFT FUNCTION
      if (this.nft_image_updated) {
        fd.append("image", this.nft_image_updated, this.nft_image_updated.name);
      }
      fd.append("nft_id", this.nft_to_update.id);
      fd.append("title", this.nft_title_updated);
      fd.append("description", this.nft_description_updated);
      fd.append("link", this.nft_link_updated);
      fd.append("properties", JSON.stringify(this.nft_props_updated));
      // Token
      let url = this.$store.state.gf_contracts.backend;
      const token = this.$store.state.user_auth_w2.token;
      axios.defaults.headers.common["Authorization"] = "Token " + token;
      await axios
        .post(url + "/update_nft_simple_collection", fd)
        .then((res) => {
          console.log("RES UPDATED:", res);
          if (res.data.nft_updated) {
            this.$emit("updateNFTS", true);
            this.closeTab();
          }
        });
    },
    async getImageFromURL(url) {
      fetch(url).then(async (response) => {
        const contentType = response.headers.get("content-type");
        const blob = await response.blob();
        // Allowed extension
        let item = this.$store.state.helpers.TYPE_TO_EXTENSION[contentType];
        if (typeof item === "undefined") {
          // Type not allowed
          const notification = {
            type: "error",
            message: "The given type is not allowed",
          };
          this.$store.dispatch("notification/add", notification, {
            root: true,
          });
          return null;
        }
        // Next file transformations
        const file = new File([blob], "img" + item, { contentType });
        if (!file) {
          console.log("no file");
          return null; // There is no file
        }
        // Too large size
        if (file.size > this.$store.state.helpers.MAX_FILE_SIZE) {
          // Allowed extension
          const msg = `File is too large. Max size is ${
            this.$store.state.helpers.MAX_FILE_SIZE / 1000000
          }MB`;
          const notification = {
            type: "error",
            message: msg,
          };
          this.$store.dispatch("notification/add", notification, {
            root: true,
          });
        }
        // access file here
        if (blob) {
          this.nft_image_updated = file;
        } else {
          this.nft_image_updated = null;
        }
        // this.image_to_upload = blob;
      });
    },
    toUpperCase(text) {
      if (text.length > 1) {
        return text.charAt(0).toUpperCase() + text.slice(1);
      } else {
        return text.toUpperCase();
      }
    },
  },
  mounted() {
    this.show_window = this.show;
    try {
      this.nft_to_update = this.existing_nft;
      this.nft_props_updated = this.nft_to_update.properties;
      this.nft_title_updated = this.nft_to_update.title;
      this.nft_description_updated = this.nft_to_update.description;
      this.nft_link_updated = this.nft_to_update.link;
      //   this.post_to_update.new_tags = this.post_to_update.tags;

      // Loading the image
      this.getImageFromURL(this.nft_to_update.image);
    } catch (error) {
      console.log("EEEOR");
      this.closeTab();
    }
  },
  watch: {
    show: function (newVal) {
      try {
        this.show_window = newVal;
      } catch (error) {
        console.log("EEEE");
        this.closeTab();
      }
    },
  },
};
</script>

<style scoped>
.img_drop_1 {
  margin: 10px 0px 40px 0px !important;
}
.space_top {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 10px;
  margin: 30px 0px 0px 0px !important;
}
.help_props {
  margin: 5px 0px 5px 0px;
  color: #0071da;
}
.properties {
  display: flex;
  justify-content: center;
  flex-direction: column;
  background-color: #f7fbff;
  width: 100%;
  height: fit-content;
  border-radius: 10px;
  margin: 30px 0px 10px 0px;
  padding: 20px;
}
.properties .subtitle__text {
  margin: 5px 0px 0px 0px;
}
.properties_container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 0px;
  margin: 0px;
  width: 100%;
  height: fit-content;
}

.property_item {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 45px;
  margin: 15px 0px 0px 0px;
  gap: 15px;
}
.star {
  font-weight: 700;
  color: #c70039;
  margin: 0px 4px;
  padding: 0px;
}
.subtitle__text img {
  padding: 0px 0px 0px 10px;
  height: 18px;
  widows: 18px;
  cursor: pointer;
}
.required {
  margin: 0px 0px 20px 0px !important;
}
.title__text {
  text-align: left;
  margin: 0px 0px 50px 0px;
  font-size: 30px;
  line-height: 40px;
  color: #252525;
  font-weight: 600;
}
.generic__text {
  font-size: 14px;
  margin: 0px 0px 5px 0px;
}
.subtitle__text,
.generic__text {
  text-align: left;
  display: flex;
  padding: 0px;
}
.subtitle__text {
  margin: 30px 0px 5px 0px;
  align-items: center;
}
.generic_label {
  display: flex;
  padding: 0px 10px;
  color: #6e6e75;
  cursor: text;
  z-index: 90;
  font-size: 14px;
  margin: 20px 0px 5px 0px;
}
.update_container {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: rgb(0, 0, 0, 0.7);
  z-index: 9999;
  left: 0px;
  top: 0px;
  margin: 0px;
  box-sizing: border-box;
  padding: 10px 10px 10px 10px;
}
.update_window {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: flex-start;
  box-sizing: border-box;
  padding: 30px;
  background: white;
  border-radius: 16px;
  width: 100%;
  max-width: 60rem;
  height: 100%;
  max-height: 70vh;
  overflow-y: auto;
  z-index: 9999;
}
.update_window::-webkit-scrollbar {
  width: 10px;
  background: white;
  cursor: pointer;
  border-radius: 0px 16px 16px 0px;
  border-left: 1px solid #ebe9e9; /*#f2f2f2;*/
}
.update_window::-webkit-scrollbar-thumb {
  background: #6e6e75;
  border-radius: 0px 16px 16px 0px;
  cursor: pointer;
}
.scrollable_container {
  position: relative;
  width: 100%;
  height: fit-content;
  background: white;
  box-sizing: border-box;
  padding: 30px 30px 100px 30px;
}
.exit_button {
  display: flex;
  justify-content: flex-end;
  position: sticky;
  right: 30px !important;
  top: 0px;
  font-size: 21px;
  font-weight: 500;
  z-index: 9998;
}
.exit_button_img {
  position: relative;
  display: flex;
  height: 31px;
  width: 31px;
  justify-content: center;
  align-content: center;
  color: #c70039;
  cursor: pointer;
  border-radius: 50%;
  transition-duration: 0.3s;
  background-color: white;
}
.exit_button_img img {
  position: relative;
  top: 4px;
  left: 1px;
  height: 24px;
  width: 24px;
}
@media (max-width: 450px) {
  .update_window {
    padding: 0px;
  }
  .exit_button {
    box-sizing: border-box;
    padding: 10px 10px 0px 0px;
  }
}
@media (hover: hover) {
  .exit_button:hover {
    font-weight: 500;
  }
  .exit_button_img:hover {
    color: #bb0a21;
    transition-duration: 0.5s;
  }
}
</style>
