<template>
  <div class="profile_container">
    <gf-profile-main-menu :selectedOption="'Services'"></gf-profile-main-menu>
    <div class="all_container">
      <div class="central_container">
        <h1 class="subtitle_text">All Grifenix Services</h1>
        <div class="toolbar_filter">
          <gf-search-input
            v-model="search_value"
            :shadow="false"
            :border="true"
            :margin="false"
            :border_right="false"
            :max_width="true"
            class="search_input"
          ></gf-search-input>
          <div class="favorites_text">
            <gf-checkbox v-model="favorites" :shadow="false"></gf-checkbox>
            <span class="generic_text">Favorites</span>
          </div>
        </div>
        <div class="templates_container">
          <div
            class="template_container"
            v-for="(template, key) in filteredTemplates"
            :key="key"
          >
            <div class="template_data">
              <div class="favorite_container">
                <div
                  class="favorite_btn"
                  @click="changeFavoriteTematics(template.id)"
                >
                  <img
                    v-if="favoriteTemplates.includes(template.id)"
                    src="@/assets/icons/generic/favorite_active.svg"
                    alt=""
                  />
                  <img
                    v-else
                    src="@/assets/icons/generic/favorite_inactive.svg"
                    alt=""
                  />
                </div>
              </div>
              <div class="template_img">
                <!-- <img :src="require('./../../' + template.icon)" alt="" /> -->
                <img :src="template.icon" alt="" />
              </div>
              <h1 class="subtitle_text">{{ template.title }}</h1>
              <h2 class="generic_text">{{ template.description }}</h2>
            </div>
            <div class="ctas">
              <gf-generic-button
                @click="toggleInfoVisibility(key)"
                :label="'More info'"
              ></gf-generic-button>
              <gf-cta-button
                :class="{ inactive: [1, 2, 3].includes(template.id) }"
                @click="this.$router.push(template.link)"
                :label="template.goto_btn"
              ></gf-cta-button>
            </div>
            <!-- More info -->
            <div class="more_info" :id="'more_info_' + key">
              <div class="info_wrapper">
                <div class="info_section">
                  <div class="info_property">
                    <h1 class="label_text">Full description</h1>
                    <h2 class="generic_text">
                      {{ template.full_description }}
                    </h2>
                  </div>
                  <div class="info_property">
                    <h1 class="label_text">Use cases</h1>
                    <h2 class="generic_text">{{ template.use_case }}</h2>
                  </div>
                  <div class="thin_sep"></div>
                  <div class="info_property">
                    <h1 class="label_text">Services</h1>
                    <div v-for="(item, k) in template.services" :key="k">
                      <h2 class="generic_text item">{{ item }}</h2>
                    </div>
                  </div>
                </div>
                <div class="btn_close">
                  <div @click="toggleInfoVisibility(key)">
                    <img
                      src="@/assets/icons/navbar/go_arrow_left_dark.svg"
                      alt=""
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <fut></fut>
</template>

<script>
import fut from "@/components/Footer";
export default {
  components: {
    fut,
  },
  data() {
    return {
      search_value: "",
      favorites: false,
      user_templates: [
        {
          id: 0,
          icon: "https://grifenix-com.github.io/G-Images/icons/services/simple_collection_utility.png",
          // icon: "assets/icons/collections/simple_collection.png",
          title: "Personalized NFT collection manager",
          favorite: true,
          description:
            "Create and distribute a fully personalized and self-custody utility NFT collections for your business cases totally no-code or on-demand.",
          full_description:
            "With this service you can create and personalize your own NFT ERC-721 and ERC-1155 collections using our no-code templates or modify some parts on-demand by contacting us. Alfter creating the collection, you can share it with your commuity via our custom Marketplace or using our SDK to implement it in your own product.",
          use_case:
            "You can create an NFT collection for your brand, to share it with your best clients and grow their relation to your company. Then you can use this NFTs to share",
          services: [
            "Create different NFT collections on ERC-721 or ERC1155",
            "Personalized Marketplace SDK for your app without blockchain know-how",
          ],
          goto_btn: "Start creating",
          link: "/account/nft/builder",
        },
        {
          id: 1,
          icon: "https://grifenix-com.github.io/G-Images/icons/services/simple_traceability.png",
          // icon: "assets/icons/collections/simple_collection_utility.png",
          title: "Trusted traceability",
          favorite: false,
          description:
            "Manage your production by adding traceability with the smart contracts, so you can ensure your users all the processes of production.",
          full_description:
            "Manage your production by adding the smart contract traceability, so you can show your users all the processes of production. You will be provided with a specific dashboard to manage all the related things to the community marketing.",
          use_case:
            "Create a specific NFT with all the traceability about how the wine bottle was created, add some extra utility like price discount or the possibility to open some thing to the user that buys your wine bottles.",
          services: [
            "Create rare NFTs for your traceability.",
            "Add analitycs to your traceability diagram and see how to improove themarketing",
          ],
          goto_btn: "Create traceability",
          link: "/account",
        },
        {
          id: 2,
          icon: "https://grifenix-com.github.io/G-Images/icons/services/qr_reader.png",
          // icon: "assets/icons/collections/simple_collection_utility.png",
          title: "NFT utility reader",
          favorite: false,
          description:
            "Scan utility NFTs and verify if the user has one item with some extra utility on it. You can also use the SDK to check the user allowance and allow him some actions.",
          full_description: "-",
          use_case: "F",
          services: [
            "Title.",
            "Description.",
            "Price",
            "Image Link/Bytecode.",
            "Utility contract addresses which you can add later.",
          ],
          goto_btn: "Let's check NFTs",
          link: "/account",
        },
        {
          id: 3,
          icon: "https://grifenix-com.github.io/G-Images/icons/services/marketplace.png",
          // icon: "assets/icons/collections/simple_collection_utility.png",
          title: "Marketplace SDK",
          favorite: false,
          description:
            "Same as a 'Simple collection' but you can also add a utility contract which will handle some unique utilities you want and can edit later.",
          full_description: "-",
          use_case: "F",
          services: [
            "Title.",
            "Description.",
            "Price",
            "Image Link/Bytecode.",
            "Utility contract addresses which you can add later.",
          ],
          goto_btn: "Create collection",
          link: "/account/collection/create_simple_collection_utility",
        },
      ],
    };
  },
  methods: {
    scrollToTop() {
      window.scroll({
        top: 0,
        left: 0,
        behavior: "smooth",
      });
    },
    capitalize(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    },
    setOption(option) {
      let links = this.$refs.creator_content.children;
      for (let i = 0; i < links.length; i++) {
        links[i].classList.remove("active");
        // Adding to the actived link
        option = option.trim();
        // Adding active class
        let title = links[i].children[0].innerHTML.trim();
        if (title === option) {
          this.creator_menu.selected_option = option;
          links[i].classList.add("active");
        }
      }
    },
    toggleInfoVisibility(id) {
      let infoCard = document.getElementById("more_info_" + id);
      console.log(infoCard);
      if ([...infoCard.classList].includes("active")) {
        infoCard.classList.remove("active");
      } else {
        infoCard.classList.add("active");
      }
    },
    changeFavoriteTematics(id) {
      this.$store.dispatch("account_data/changeFavoriteTemplates", id, {
        root: true,
      });
    },
  },
  computed: {
    favoriteTemplates: function () {
      const favs = this.$store.state.account_data.data.favorite_templates;
      console.log("Favs:", favs);
      return favs;
    },
    filteredTemplates: function () {
      return this.user_templates.filter((temp) => {
        // Favorites filter
        let isGood = false;
        let favorites = this.$store.state.account_data.data.favorite_templates;
        if (this.favorites && favorites && favorites.length > 0) {
          if (favorites.includes(temp.id)) {
            return true;
          } else {
            return false;
          }
        }

        let search = this.search_value.toLowerCase();
        if (
          temp.title.toLowerCase().match(search) ||
          temp.description.toLowerCase().match(search)
        ) {
          isGood = isGood || true;
        }
        return isGood;
      });
    },
  },
  watch: {
    filtered: function () {
      this.filteredTemplates;
    },
  },
  created() {
    // this.$store.dispatch("account_data/getUserServerCollections", {
    //   root: true,
    // });
    this.scrollToTop();
  },
};
</script>

