  position: relative;
  height: 80px;
  width: 80px;
}
.more_info {
  position: absolute;
  box-sizing: border-box;
  padding: 5px;
  border-radius: 16px;
  left: 0px;
  width: 100%;
  height: 100%;
  top: 0px;
  transform: translateY(-110%);
  transition-duration: 0.3s;
  /* border: 1px solid #0071da; */
  /* border: 1px solid #ebe9e9 !important; */
