  height: fit-content;
  box-sizing: border-box;
  padding: 15px 0px 0px 0px;
}
.info_property .item {
  position: relative;
  box-sizing: border-box;
  padding: 0px 0px 0px 10px;
}
.info_property .item::before {
  position: absolute;
  left: 0px;
  top: 9px;
  content: "";
  background-color: #6e6e73;
  border-radius: 50%;
  height: 5px;
  width: 5px;
}
.info_property h1,
.info_property h2 {
  text-align: left !important;
  justify-content: flex-start !important;
